import {Component, ViewChild} from '@angular/core';
import {Storage} from '@ionic/storage';
import {IonicPage, MenuController, NavController, Slides} from 'ionic-angular';
import {AppConfigProvider} from '../../providers/appconfig/appconfig';

@IonicPage()
@Component({
  selector: 'page-tutorial',
  templateUrl: 'tutorial.html',
})
export class TutorialPage {
  showSkip = false;

  @ViewChild('slides') public slides: Slides;

  sted = 'TenOase';
  BoJoKo: boolean;

  constructor(
    private navCtrl: NavController,
    private menu: MenuController,
    private storage: Storage,
    private apc: AppConfigProvider,
  ) {
    this.apc.setarrangement(this.sted);
    this.subscribeTo(this.sted);
    this.BoJoKo = this.apc.isSubscribedToTrack('BoJoKo');
  }

  startApp() {
    this.navCtrl.setRoot('TabsPage').then(() => {
      this.navCtrl.popToRoot();
      this.storage.set('hasSeenTutorial', 'true');
      this.storage.set('v1.1.0', true);
    });
  }

  onSlideChangeStart(slider: Slides) {
    this.showSkip = !slider.isEnd();
  }

  ionViewWillEnter() {
    this.slides.update();
  }

  ionViewDidEnter() {
    this.menu.enable(false);
  }

  ionViewDidLeave() {
    this.menu.enable(true);
  }

  nextSlide() {
    this.slides.slideNext();
  }

  subscribeTo(topic: string) {
    this.apc.addTrack(topic);
  }

  togggleChildren() {
    if (this.BoJoKo === true) {
      this.subscribeTo('BoJoKo');
    } else {
      this.unsubscribeFrom('BoJoKo');
    }
  }

  unsubscribeFrom(topic: string) {
    this.apc.removeTrack(topic);
  }

  clearTracks() {
    this.apc.clearAllTracks();
  }

}



